import React from 'react';
import {Redirect} from 'react-router-dom';
import Axios from 'axios';

Axios.defaults.withCredentials = true;

class Signin extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      username: '',
      password: '',
      message: '',
      success_message: ''
    }
  }

  changeHandler = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  clickHandler = (e) => {
    if (e.target.value === "signin") {
      this.setState({
        message: '',
        success_message: ''
      })

      Axios.post('/auth/signin', {
        username: this.state.username,
        password: this.state.password
      }).then((res) => {
        if(res.data.success === true){
          this.props.loginHandler(res.data.user);

          Axios.post('/api/notes/my', {}).then((res) => {
            this.props.notesHandler(res.data.notes);
          })
        }else {
          this.setState({
            message: res.data.message
          });
        }
      })
    } else if (e.target.value === "signup") {
      this.setState({
        message: '',
        success_message: ''
      })

      Axios.post('/auth/signup', {
        username: this.state.username,
        password: this.state.password
      }).then((res) => {
        if(res.data.success === true){
          this.setState({
            success_message: res.data.message
          })
        }else{
          this.setState({
            message: res.data.message
          });
        }
      })
    }
  }

  submitHandler = (e) => {
    e.preventDefault();
  }

  componentDidMount(){
    document.title = 'Sign in'
  }

  componentWillUnmount(){
    document.title = 'NTU Note'
  }

  render(){
    if (this.props.user){
      return <Redirect to="/"/>
    }

    return (
      <div className="section is-medium">
        <div className="columns">
          <div className="column">
          </div>
          <div className="column">
            <h1 className="title is-size-4">Sign in</h1>
            <form onSubmit={this.submitHandler}>
              <div className="field">
                <label className="label">Username</label>
                <div className="control">
                  <input
                    name="username"
                    className="input"
                    type="text"
                    placeholder="Username"
                    value={this.state.username}
                    onChange={this.changeHandler}
                    required
                  />
                </div>
              </div>

              <div className="field">
                <label className="label">Password</label>
                <div className="control">
                  <input
                    name="password"
                    className="input"
                    type="password"
                    placeholder="Password"
                    value={this.state.password}
                    onChange={this.changeHandler}
                    required
                  />
                </div>
              </div><br/>

              <div className="field is-grouped">
                <div className="control">
                  <button type="submit" value="signin" className="button is-info" onClick={this.clickHandler}>Sign in</button>
                </div>
                <div className="control">
                  <button type="submit" value="signup" className="button is-light" onClick={this.clickHandler}>Sign up</button>
                </div>
              </div>
              <p class="help is-danger"> {this.state.message}</p>
              <p class="help is-success"> {this.state.success_message}</p>
            </form>
          </div>
          <div className="column">
          </div>
        </div>
      </div>
    )
  }
}

export default Signin;
