import React from 'react';
import Axios from 'axios';
import {Redirect} from 'react-router-dom';

Axios.defaults.withCredentials = true;

class Settings extends React.Component {
  constructor(props){
    super(props);

    if (props.user) {
      this.state = {
        username: props.user.username,
        profile: props.user.profile ?? '',
        new_password: '',
        password: '',
        message: '',
        success_message: ''
      }
    }
  }

  submitHandler = (e) => {
    this.setState({
      message: '',
      success_message: ''
    });

    e.preventDefault();

    Axios.post('/auth/update', {
      user_id: this.props.user._id,
      username: this.state.username,
      new_password: this.state.new_password,
      profile: this.state.profile,
      password: this.state.password
    }).then((res) => {
      console.log(res.data)

      if(res.data.success === true){
        this.props.loginHandler(res.data.user);
        this.setState({
          success_message: res.data.message
        })
      }else{
        this.setState({
          message: res.data.message
        })
      }
    })
  }

  componentDidMount(){
    document.title = 'Settings';
  }

  componentWillUnmount(){
    document.title = 'NTU Note';
  }

  changeHandler = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  render(){
    if (!this.props.user) {
      return <Redirect to="/signin"/>
    }

    return (
      <div class="section">
        <div class="columns">
          <div class="column">
          </div>
          <div class="column">
            <h1 class="title is-size-4">Settings</h1>
            <form onSubmit={this.submitHandler}>
              <div class="field">
                <label class="label">Username</label>
                <div class="control">
                  <input
                    name="username"
                    class="input"
                    type="text"
                    placeholder="Username"
                    value={this.state.username}
                    onChange={this.changeHandler}
                    required
                  />
                </div>
              </div>

              <div class="field">
                <label class="label">New Password</label>
                <div class="control">
                  <input
                    name="new_password"
                    class="input"
                    type="password"
                    placeholder="Left blank to remain"
                    onChange={this.changeHandler}
                  />
                </div>
              </div>

              <div class="field">
                <label class="label">Profile</label>
                <div class="control">
                  <textarea
                    name="profile"
                    class="textarea"
                    placeholder="Personal Profile"
                    value={this.state.profile}
                    onChange={this.changeHandler}
                  />
                </div>
              </div>


              <div class="field">
                <label class="label">Password</label>
                <div class="control">
                  <input
                    name="password"
                    class="input"
                    type="password"
                    onChange={this.changeHandler}
                    placeholder="We need you password to make changes"
                    required
                  />
                </div>
              </div><br/>

              <div class="field is-grouped">
                <div class="control">
                  <button type="submit" class="button is-info">Submit</button>
                </div>
              </div>
              <p class="help is-danger"> {this.state.message}</p>
              <p class="help is-success"> {this.state.success_message}</p>
            </form>
          </div>
          <div class="column">
          </div>
        </div>
      </div>
    )
  }
}

export default Settings;
