import React, {useEffect, useState} from 'react';
import {Link, useParams} from 'react-router-dom';
import Axios from 'axios';

Axios.defaults.withCredentials = true;

function Profile(props) {
  const {id} = useParams();
  const [notes, setNotes] = useState([]);
  const [username, setUsername] = useState();
  const [profile, setProfile] = useState();

  useEffect(() => {
    document.title = 'Profile';

    Axios.post(`/user/info`, {
      user_id: id
    }).then((res) => {
      setUsername(res.data.username);
      setProfile(res.data.profile);
      setNotes(res.data.notes);
    });

    return () => {
      document.title = 'NTU Note';
    }
  }, []);

  return (
    <div class="content">
      <div class="section">
        <div class="columns">
          <div class="column">
          </div>
          <div class="column is-7">
            <p>
              <strong class="is-size-3">{username}</strong> &nbsp; <small class="is-size-5">{notes.length} Public Notes</small>
            </p><br/>
            <p>
              <label class="label">Profile</label>
              {profile ?? "This user hasn't set his or her profile."}
              <br/><br/>
              <label class="label">Public Notes</label>
              <ul>
                {
                  notes.map((note) => (
                    <li>
                      <Link to={`/note/${note.id}`}>{note.title}</Link>
                    </li>
                  ))
                }
              </ul>
            </p>
          </div>
          <div class="column">
          </div>
        </div>
      </div>
    </div>
  )
}

export default Profile;
