import 'codemirror/lib/codemirror.css';
import '@toast-ui/editor/dist/toastui-editor.css';

import React, { useEffect } from 'react';
import { Editor } from '@toast-ui/react-editor';
import { Route, Switch, useParams, useHistory } from 'react-router-dom';
import Axios from 'axios';
import NoteSettings from './NoteSettings';

Axios.defaults.withCredentials = true;

function Note(props){
  let editorRef = React.createRef();
  let {id} = useParams();
  let history = useHistory();
  var time_stamp = 0;


  const changeHandler = () => {
    document.title = 'Saving...';
    setTimeout(() => {
      if (time_stamp < Date.now() - 3000){
        time_stamp = Date.now();
        if (editorRef.current) {
          var content = editorRef.current.getInstance().getMarkdown();
          Axios.post('/api/notes/save', {
            nid: id,
            content: content
          }).then((res) => {
            if(res.data.success){
              document.title = res.data.note.title;
            }
          })
        }
      }
    }, 3000);
  }

  useEffect(() => {
    Axios.post('/api/notes/get', {
      id: id
    }).then((res) => {
      const {note} = res.data;

      if(!note){
        history.push('/');
        return;
      }

      console.log(note);
      document.title = note.title;

      if(!props.note || props.note._id !== note._id){
        props.noteHandler(note);
      }

      if(editorRef.current){
        editorRef.current.getInstance().setMarkdown(note.content);
      }
    })

    return () => {
      document.title = 'NTU Note';
    }
  });

  return (
    <Switch>
      <Route path={`/note/${id}/settings`}>
        <div>
          <NoteSettings note={props.note} user={props.user} noteHandler={props.noteHandler}/>
        </div>
      </Route>
      <Route path={`/note/${id}/`}>
        <div id="editor">
          <Editor
            previewStyle="vertical"
            height="90vh"
            hideModeSwitch="true"
            ref={editorRef}
            onChange={changeHandler}
          />
        </div>
      </Route>
    </Switch>
  )
}

export default Note;
