import React from 'react';
import { Switch, Route, Link, useRouteMatch, Redirect, useParams } from 'react-router-dom';
import {notes} from '../test';
import moment from 'moment';
import Axios from 'axios';

function List(props) {
  let {directory} = useParams();

  if (directory && directory !== "recent") {
    return (
      <table class="table is-hoverable is-fullwidth">
        <thead>
          <tr>
            <th></th>
            <th style={{width: "45vw"}}>Title</th>
            <th style={{width: "20vw"}}>Directory</th>
            <th style={{width: "20vw"}}>Last Modified Time</th>
          </tr>
        </thead>
        <tbody>
          {
            props.notes.filter(note => note.directory === directory).map(note => (
              <tr draggable="true" key={note.id}>
                <td><ion-icon name="document-outline"></ion-icon></td>
                <td><Link to={`/note/${note._id}`}>{note.title}</Link></td>
                <td>{ note.directory? <Link to={`/note/directory/${note.directory}`}>{note.directory}</Link>:"--"}</td>
                <td>{moment.unix(note.time_stamp / 1000).format('YYYY / MM / DD  HH:mm:ss')}</td>
              </tr>
            ))
          }
        </tbody>
      </table>
    )
  }

  return (
    <table class="table is-hoverable is-fullwidth">
      <thead>
        <tr>
          <th></th>
          <th style={{width: "45vw"}}>Title</th>
          <th style={{width: "20vw"}}>Directory</th>
          <th style={{width: "20vw"}}>Last Modified Time</th>
        </tr>
      </thead>
      <tbody>
        {
          props.notes.map(note => (
            <tr draggable="true" key={note.id}>
              <td><ion-icon name="document-outline"></ion-icon></td>
              <td><Link to={`/note/${note._id}`}>{note.title}</Link></td>
              <td>{ note.directory? <Link to={`/note/directory/${note.directory}`}>{note.directory}</Link>:"--"}</td>
              <td>{moment.unix(note.time_stamp / 1000).format('YYYY / MM / DD  HH:mm:ss')}</td>
            </tr>
          ))
        }
      </tbody>
    </table>
  )
}

class Notes extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      notes: []
    }
  }

  componentDidMount(){
    Axios.post('/api/notes/my', {}).then((res) => {
      this.setState({
        notes: res.data.notes
      })
    })
  }

  render() {
    if (!this.props.user) {
      return <Redirect to="/signin"/>
    }

    return (
      <div className="columns" id="content">
        <div className="column is-2">
          <Menu notes={this.state.notes}/>
        </div>
        <div className="column is-10">
          <Switch>
            <Route path="/notes/:directory">
              <List notes={this.state.notes}/>
            </Route>
            <Route path="/notes/recent">
              <List notes={[...this.state.notes].sort((a, b) => (a.time_stamp < b.time_stamp)? 1:-1)}/>
            </Route>
            <Route path="/notes">
              <List notes={this.state.notes}/>
            </Route>
          </Switch>
        </div>
      </div>
    );
  }
}

function directories(notes) {
  var res = []
  for (const note of notes) {
    if (note.directory && !(note.directory in res)) {
      res.push(note.directory)
    }
  }

  return res;
}

function Menu(props) {
  let match = useRouteMatch();

  return (
      <aside className="menu">
        <p className="menu-label">
          Search
        </p>
        <div className="field">
          <p className="control has-icons-left">
            <input className="input is-small is-rounded" type="text" placeholder="Title, Content or Tags"/>
            <span className="icon is-small is-left">
              <ion-icon name="search-outline"></ion-icon>
            </span>
          </p>
        </div>
        <p className="menu-label">
          General
        </p>
        <ul className="menu-list">
          <li><Link to={`${match.url}`}>All</Link></li>
          <li><Link to={`${match.url}/recent`}>Recent</Link></li>
        </ul>
        <p className="menu-label">
          Directories
        </p>
        <ul className="menu-list">
          {
            directories(props.notes).map((directory) => (
              <li><Link to={`/notes/${directory}`}>{directory}</Link></li>
            ))
          }
        </ul>
      </aside>
  )
}

export default Notes;
