import React from 'react';
import { Link, Switch, Route, useHistory, useParams} from 'react-router-dom';
import Axios from 'axios';

Axios.defaults.withCredentials = true;

function RecentNotes(props) {
  return (
    [...props.notes].sort((a, b) => ((a.time_stamp < b.time_stamp)? 1:-1)).map(note => (
      <Link to={`/note/${note._id}`} className="navbar-item">
        <ion-icon name="document-outline"></ion-icon> &nbsp; {note.title}
      </Link>
    ))
  )
}

function NoteSettings(props){
  let {id} = useParams();

  return (
    <div class="navbar-item has-dropdown is-hoverable">
      <Link class="navbar-link" to={`/note/${id}`}>{props.note && props.note.title}</Link>
      <div class="navbar-dropdown is-boxed is-right">
        <Link class="navbar-item" to={`/note/${id}/settings`}>
          <ion-icon name="cog-outline"></ion-icon> &nbsp; Note Settings
        </Link>
        <hr class="navbar-divider" />
        <div class="navbar-item">
          <ion-icon name="eye-outline"></ion-icon> &nbsp;&nbsp;{props.note && (props.note.public? "Public":"Private")} Note
        </div>
        <div class="navbar-item">
          <ion-icon name="link-outline"></ion-icon> &nbsp;&nbsp; <a href={`/note/${props.note && props.note._id}`}>Permalink</a>
        </div>
      </div>
    </div>
  );
}

function NoteUtils(props) {
  let history = useHistory();

  return (
    <Switch>
      <Route path="/note/:id">
        <NoteSettings note={props.note}/>
      </Route>
      <Route path="/">
        <div class="navbar-item">
          <button
            class="button is-small is-info is-outlined"
            onClick={()=>{
              Axios.post('/api/notes/new', {}).then((res) => {
                console.log(res.data);

                if (res.data.success === true) {
                  props.noteHandler(res.data.note);
                  history.push(`/note/${res.data.note._id}`)
                }
              })
            }}
          > New Note </button>
        </div>
      </Route>
    </Switch>
  )
}

class Navbar extends React.Component {
  constructor(props){
    super(props)
  }

  render() {
    return (
      <nav className="navbar is-transparent" role="navigation" aria-label="main navigation">
        <div className="navbar-brand">
          <Link className="navbar-item" to="/">
            <b>NTU Note</b>
          </Link>

          <a role="button" className="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div className="navbar-menu">
          <div className="navbar-start">
            {
              this.props.user &&

              <React.Fragment>
                <Link className="navbar-item" to="/notes" >
                  My Notes
                </Link>

                <div className="navbar-item has-dropdown is-hoverable" href="/">
                  <div className="navbar-link"> Recent </div>
                  <div className="navbar-dropdown is-boxed">
                    <RecentNotes notes={this.props.notes}/>
                    <hr className="navbar-divider"/>
                    <Link className="navbar-item" to="/notes/recent">
                      <ion-icon name="ellipsis-horizontal-outline"></ion-icon> &nbsp; Other Recent Notes
                    </Link>
                  </div>
                </div>
              </React.Fragment>
            }
          </div>

          <div className="navbar-end">
            {
              this.props.user?

              <React.Fragment>
                <NoteUtils note={this.props.note} noteHandler={this.props.noteHandler}/>
                <div className="navbar-item has-dropdown is-hoverable" href="/">
                  <div className="navbar-link">
                    <span class="tag"><b>{this.props.user.username[0]}</b></span>&nbsp; {this.props.user.username}
                  </div>
                  <div className="navbar-dropdown is-boxed is-right">
                    <Link className="navbar-item" to={`/profile/${this.props.user._id}`}><ion-icon name="person-outline"></ion-icon> &nbsp; Profile</Link>
                    <Link className="navbar-item" to="/settings"><ion-icon name="cog-outline"></ion-icon> &nbsp; Settings</Link>
                    <hr className="navbar-divider"/>
                    <a className="navbar-item" onClick={this.props.logoutHandler}><ion-icon name="log-out-outline"></ion-icon> &nbsp; Sign out</a>
                  </div>
                </div>
              </React.Fragment>:<React.Fragment>
                <div className="navbar-item">
                  <Link className="navbar-item" to="/signin">Sign in</Link>
                </div>
              </React.Fragment>
            }
          </div>
        </div>
      </nav>
    )
  }
}

export default Navbar;
