import './App.css';
import React from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import Navbar from './components/Navbar';
import Notes from './pages/Notes';
import Note from './pages/Note';
import Settings from './pages/Settings';
import Signin from './pages/Signin';
import Profile from './pages/Profile';
import NoteSettings from './pages/NoteSettings';
import Home from './pages/Home';
import Axios from 'axios';

Axios.defaults.withCredentials = true;

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      user: null,
      note: null,
      notes: []
    }
  }

  componentDidMount() {
    Axios.post('/auth/signed_in').then((res) => {
      console.log(res);
      if(res.data.signed_in === true){
        this.setState({
          user: res.data.user
        })
      }

      Axios.post('/api/notes/my').then((res) => {
        if (res.data.success) {
          this.setState({
            notes: res.data.notes
          })
        }
      })
    })
  }

  loginHandler = (user) => {
    this.setState({
      user: user
    })
  }

  noteHandler = (note) => {
    this.setState({
      note: note
    })
  }

  notesHandler = (notes) => {
    this.setState({
      notes: notes
    })
  }

  logoutHandler = () => {
    this.setState({
      user: null
    })

    Axios.post('/auth/signout');
  }

  render(){
    return (
      <Router>
        <div className="App">
          <Navbar user={this.state.user} note={this.state.note} notes={this.state.notes} noteHandler={this.noteHandler} logoutHandler={this.logoutHandler}/>
          <Switch>
            <Route path="/profile/:id">
              <Profile user={this.state.user}/>
            </Route>
            <Route path="/note/:id" id="note">
              <Note user={this.state.user} note={this.state.note} noteHandler={this.noteHandler}/>
            </Route>
            <Route path="/notes">
              <Notes user={this.state.user} notes={this.state.notes}/>
            </Route>
            <Route path="/signin">
              <Signin user={this.state.user} loginHandler={this.loginHandler} notesHandler={this.notesHandler}/>
            </Route>
            <Route path="/settings">
              <Settings user={this.state.user} loginHandler={this.loginHandler}/>
            </Route>
            <Route path='/'>
              <Home/>
            </Route>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
