import React from 'react';
import { useParams, Link, Redirect } from 'react-router-dom';
import Axios from 'axios';

class NoteSettings extends React.Component {
  constructor(props){
    super(props);

    if(props.note){
      this.state = {
        title: props.note.title,
        directory: props.note.directory,
        new_directory: '',
        public: props.note.public? 'true':'false',
        delete_check: ''
      }
    }else{
      this.state = {
        title: '',
        directory: '',
        public: 'false',
        delete_check: ''
      }
    }
  }

  changeHandler = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  deleteHandler = (e) => {
    e.preventDefault();

    if (this.props.note) {
      Axios.post('/api/notes/delete', {
        nid: this.props.note._id
      }).then((res) => {
        if (res.data.success) {
          window.location = '/'
        }
      })
    }

  }

  componentWillReceiveProps(props) {
    if(props.note){
      this.setState({
        title: props.note.title,
        directory: props.note.directory,
        public: props.note.public? "true":"false",
        delete_check: ''
      })
    }
  }

  submitHandler = (e) => {
    e.preventDefault();

    Axios.post('/api/notes/update', {
      nid: this.props.note._id,
      title: this.state.title,
      directory: this.state.directory,
      public: (this.state.public === "true")
    }).then((res) => {
      console.log(res.data.note);
      if(res.data.success){
        this.props.noteHandler(res.data.note);
      }
    })
  }

  render() {
    if (!this.props.user || this.props.note && this.props.user && this.props.note.author !== this.props.user._id){
      return <Redirect to="/"/>
    }

    return (
      <div class="section">
        <div class="columns">
          <div class="column">
          </div>
          <div class="column">
            <h1 class="title is-size-4">Note Settings</h1>
            <form onSubmit={this.submitHandler}>
              <div class="field">
                <label class="label">Title</label>
                <div class="control">
                  <input
                    name="title"
                    class="input"
                    type="text"
                    value={this.state.title}
                    onChange={this.changeHandler}
                  />
                </div>
              </div>

              <div class="field">
                <label class="label">Directory</label>
                <div class="control">
                  <input
                    name="directory"
                    class="input"
                    type="text"
                    value={this.state.directory}
                    onChange={this.changeHandler}
                  />
                </div>
              </div>

              <div class="field">
                <label class="label">Visibility</label>
                <div class="control">
                  <div class="select">
                    <select name="public" value={this.state.public} onChange={this.changeHandler}>
                      <option value="true">Public</option>
                      <option value="false">Private</option>
                    </select>
                  </div>
                </div>
              </div>

              <br/>

              <div class="field">
                <label class="label">Delete Note</label>
                <div class="field is-grouped">
                  <p class="control is-expanded">
                    <input
                      name="delete_check"
                      class="input"
                      type="text"
                      placeholder="Type complete title here to delete note"
                      onChange={this.changeHandler}
                      value={this.state.delete_check}
                    />
                  </p>
                  <p class="control">
                    <button
                      class="button is-danger"
                      disabled={!(this.props.note && this.state.delete_check === this.props.note.title)}
                      onClick={this.deleteHandler}
                    >
                      Delete
                    </button>
                  </p>
                </div>
              </div>


              <br/>

              <div class="field is-grouped">
                <div class="control">
                  <button type="submit" class="button is-info">Save Changes</button>
                </div>
              </div>
            </form>
          </div>
          <div class="column">
          </div>
        </div>
      </div>
    )
  }
}

export default NoteSettings;
